.f--description {
  h2 {
    @include font-regular;
    @include margin(null null rem(10) null);
    @include font-size(22, 28);

    @include mq($bp768) {
      @include font-size(26, 32);
      @include margin(null null rem(15) null);
    }
  }


  p {
    @include font-regular;
    @include font-size(16, 24);
    color: $color-neutral-white;

    @include mq($bp768) {
      @include font-size(18, 26);
    }

    strong {
     @include font-bold;
    }

    i,
    em {
      font-style: italic;
    }

    a {
      color: inherit;
      text-decoration: underline;

      @include hover {
        text-decoration: none;
      }
    }
  }
}
