.cc--text-cta {
  @include maxwidth(1440px);

  .c--text-cta {
    @include padding(rem(50) null rem(100));

    @include mq($bp1024) {
      @include padding(rem(100) null rem(150));
    }

    .f--description {
      @include margin(null null rem(50));
      @include padding(null cols(2, 12));
      text-align: center;

      @include mq($bp768) {
        @include padding(null cols(6));
      }

      p {
        @include font-medium;
        @include font-size(18, 22);
        color: $color-neutral-gray-60;

        @include mq($bp768) {
          @include font-size(26, 34);
        }
      }

      a {
        color: $color-primary-main;
        text-decoration: none;

        @include hover {
          text-decoration: underline;
        }
      }
    }

    .f--link {
      text-align: center;

      a {
        @include button;
      }
    }
  }
}