.cc--image-hero {
  background: $color-gradient-dark;
  position: relative;

  .c--image-hero {
    @include padding(rem(60) null 56vw);

    @include mq($bp1024) {
      @include padding(rem(175) null rem(350));
    }

    .text-container {
      @include padding(null cols(1, 12));
      position: relative;

      @include mq($bp768) {
        @include padding(null 0);
      }

      .f--eyebrow {
        @include margin(null null rem(10));
        text-align: center;
        text-transform: uppercase;

        @include mq($bp1024) {
          @include centered('x');
          @include margin(null null 0);
          bottom: calc(100% + #{rem(16)});
        }

        span {
          @include font-semi-bold;
          @include font-size(18, 28);
          @include gradient-text($color-gradient-prime);

          @include mq($bp1024) {
            @include font-size(32, 40);
          }
        }
      }

      .f--page-title {
        @include margin(null null rem(50));
        text-align: center;

        h1 {
          @include font-semi-bold;
          @include font-size(40, 50);
          color: $color-neutral-white;
          letter-spacing: rem(0.8);
          text-transform: uppercase;

          @include mq($bp1024) {
            @include font-size(80, 96);
          }
        }
      }

      .f--link {
        text-align: center;

        a {
          @include button;
        }
      }
    }

    .f--image {
      @include centered('x');
      @include margin(null auto -50%);
      bottom: 0;
      width: 100%;

      @include mq($bp1024) {
        @include margin(null null 0);
        bottom: -60%;
        max-width: rem(710);
      }
    }
  }
}