.cc--icon-cards {
  @include maxwidth(1440px);

  .c--icon-cards {
    @include padding(rem(50) cols(1, 12) rem(100));

    @include mq($bp1024) {
      @include padding(rem(150) cols(6));
    }

    .f--section-title {
      @include margin(null null rem(32));
      text-align: center;

      @include mq($bp1024) {
        @include margin(null null rem(50));
      }

      h2 {
        @include font-regular;
        @include font-size(30, 40);
        color: $color-neutral-white;

        @include mq($bp1024) {
          @include font-size(60, 70);
        }
      }
    }

    .cards-container {
      @include mq($bp768) {
        display: flex;
        flex-wrap: wrap;
      }

      .f--card {
        @include card-shadow;
        @include padding(rem(44) cols(1, 10));
        border: 1px solid $color-neutral-gray-27;
        border-radius: rem(16);

        @include mq($bp768) {
          @include padding(null cols(1, 36));
          border-radius: rem(24);
          flex: 0 0 cols(11, 36);
        }

        @include mq($bp1024) {
          @include margin(null null rem(50));
        }

        &:not(:last-child) {
          @include mq($bp1024max) {
            @include margin(null null rem(32));
          }
        }

        &:not(:nth-child(3n)) {
          @include mq($bp768) {
            @include margin(null cols(1.5, 36) null null);
          }
        }

        &:nth-last-child(-n+3) {
          @include mq($bp768) {
            @include margin(null null 0);
          }
        }

        .f--image {
          @include margin(null null rem(35));
          
          img {
            @include margin(null auto);
            max-width: 100%;
            width: auto;
          }
        }

        .f--cta-title {
          @include margin(null null rem(10));
          text-align: center;

          h3 {
            @include font-semi-bold;
            @include font-size(26, 34);
            color: $color-neutral-white;
          }
        }

        .f--description {
          @include margin(null null rem(20));
          text-align: center;

          @include mq($bp1024) {
            @include margin(null null rem(30));
          }

          p {
            @include font-regular;
            @include font-size(22, 30);
            color: $color-primary-light-1;

            @include mq($bp1024) {
              @include font-size(26, 34);
            }
          }
        }

        .f--link {
          text-align: center;
  
          a {
            @include font-bold;
            @include font-size(20, 20);
            color: $color-primary-main;
            display: block;
            letter-spacing: rem(2);
            text-transform: uppercase;
            text-decoration: none;

            @include mq($bp1024) {
              @include font-size(30, 30);
            }

            @include hover {
              text-decoration: underline;
            }
          }
        }
      }
    }
  }
}