.cc--number-cards {
  @include padding(rem(100) cols(1, 12));

  @include mq($bp1200) {
    @include padding(rem(150) cols(6));
  }

  .c--number-cards {
    @include maxwidth(1440px);

    .f--section-title {
      @include margin(null null rem(32));
      text-align: center;

      @include mq($bp1200) {
        @include margin(null null rem(50));
      }

      h2 {
        @include font-regular;
        @include font-size(30, 40);
        color: $color-neutral-white;

        @include mq($bp1200) {
          @include font-size(60, 70);
        }
      }
    }

    .cards-container {
      @include mq($bp1200) {
        display: flex;
      }

      .f--card {
        @include card-shadow;
        @include padding(rem(20) null rem(24));
        border: 1px solid $color-neutral-gray-27;
        border-radius: rem(16);

        @include mq($bp1200) {
          @include padding(rem(65) null rem(50));
          border-radius: rem(24);
          flex: 0 0 cols(11, 36);
        }

        &:not(:last-child) {
          @include margin(null null rem(32));

          @include mq($bp1200) {
            @include margin(null cols(1.5, 36) 0 null);
          }
        }

        .f--cta-title {
          text-align: center;
          
          h3 {
            @include font-thin;
            @include font-size(180, 150);
            display: inline-block;
            letter-spacing: rem(-10);
            overflow: visible;
            position: relative;

            @include mq($bp1400) {
              @include font-size(230, 200);
            }

            span {
              @include font-medium;
              @include font-size(60, 80);
              position: absolute;
              left: calc(100% - #{rem(13)});
              top: 0;
              width: rem(60);

              @include mq($bp1400) {
                @include font-size(82, 105);
                width: rem(82);
              }
            }
          }
        }

        &.color-steel {
          .f--cta-title h3,
          .f--cta-title h3 span,
          .f--sub-title {
            @include gradient-text($color-gradient-steel);
          }
        }

        &.color-sky {
          .f--cta-title h3,
          .f--cta-title h3 span,
          .f--sub-title {
            @include gradient-text($color-gradient-sky);
          }
        }

        .f--sub-title {
          @include font-semi-bold;
          @include font-size(30, 40);
          @include margin(rem(20) null null);
          display: block;
          text-align: center;
          text-transform: uppercase;
        }

        .f--description {
          @include margin(rem(30) null null);
          @include padding(null cols(1, 10));
          text-align: center;
          @include mq($bp1200) {
            @include margin(rem(44) null null);
            @include padding(null cols(1, 36));
          }

          p {
            @include font-regular;
            @include font-size(22, 30);
            color: $color-primary-light-1;

            @include mq($bp1200) {
              @include font-size(26, 34);
            }
          }
        }
      }
    }
  }
}