.cc--article-hero {
  background-color: $color-neutral-gray-10;

  .c--article-hero {
    @include padding(rem(50) cols(1, 12) rem(60));

    @include mq($bp1024) {
      @include padding(rem(100) cols(12) rem(50));
    }

    .f--image {
      @include margin(null null rem(15));

      @include mq($bp1024) {
        @include margin(null null rem(40));
      }

      img {
        @include card-shadow;
        border-radius: rem(8);

        @include mq($bp1024) {
          border-radius: rem(24);
        }
      }
    }

    .f--page-title {
      @include margin(null null rem(24));
      text-align: center;

      @include mq($bp1024) {
        @include margin(null null rem(50));
        @include padding(null cols(3, 24));
      }

      h1 {
        @include font-regular;
        @include font-size(30, 40);
        color: $color-neutral-white;

        @include mq($bp1024) {
          @include font-size(60, 70);
        }
      }
    }

    .article-author {
      @include font-semi-bold;
      @include font-size(18, 28);
      color: $color-primary-dark-1;

      @include mq($bp1024) {
        @include font-regular;
        @include font-size(22, 28);
      }

      br {
        @include mq($bp1024) {
          display: none;
        }
      }

      span {
        display: none;

        @include mq($bp1024) {
          display: inline;
        }
      }
    }
  }
}