.cc--divider {
  @include padding(null cols(1, 12));

  @include mq($bp768) {
    @include padding(null cols(6));
  }

  .c--divider {
    @include maxwidth(1440px);
    background-image: linear-gradient(to right, $color-neutral-gray-60 10%, $color-neutral-black 0%);
    background-position: top;
    background-size: rem(8) rem(1);
    background-repeat: repeat-x;
    height: rem(2);
    width: 100%;
  }
}
