.cc--logo-grid-wide {
  position: relative;
  overflow: hidden;

  .c--logo-grid-wide {
    @include padding(rem(100) cols(1, 12));

    @include mq($bp1024) {
      @include padding(rem(150) cols(6));
    }
  }

  .f--section-title {
    @include margin(null null rem(20));
    text-align: center;

    @include mq($bp1024) {
      @include margin(null null rem(50));
    }

    h2 {
      @include font-regular;
      @include font-size(30, 40);
      color: $color-neutral-white;

      @include mq($bp1024) {
        @include font-size(60, 70);
      }
    }
  }

  .grid-wrapper {
    @include maxwidth(1440px);
    column-gap: rem(32);
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    row-gap: rem(5);

    @include mq($bp768) {
      grid-template-columns: repeat(5, 1fr);
    }

    @include mq($bp1200) {
      column-gap: rem(60);
      row-gap: rem(52);
    }

    .f--image {
      &:last-child {
        display: none;

        @include mq($bp768) {
          display: block;
        }
      }
    }
  }
}