.cc--basic-hero {
  overflow: hidden;
  position: relative;

  .c--basic-hero {
    .image-container {
      aspect-ratio: 1 / 1;

      @include mq($bp1024) {
        aspect-ratio: auto;
      }

      .f--image {
        height: 100%;
        position: relative;

        &::after {
          @include centered;
          background-color: rgba($color-neutral-black, 0.4);
          content: '';
          height: 100%;
          width: 100%;
        }

        img {
          height: 100%;
          object-fit: cover;
        }
      }
    }

    .text-container {
      @include centered;
      @include padding(null cols(1, 12));
      width: 100%;

      @include mq($bp1024) {
        @include padding(null cols(6));
      }

      .f--page-title {
        text-align: center;

        h1 {
          @include font-semi-bold;
          @include font-size(40, 50);
          color: $color-neutral-gray-95;
          text-transform: uppercase;

          @include mq($bp1200) {
            @include font-size(80, 96);
          }
        }
      }

      .f--description {
        @include margin(rem(10) null null);
        text-align: center;

        @include mq($bp1200) {
          @include margin(rem(40) null null);
        }

        p {
          @include font-regular;
          @include font-size(22, 30);
          color: $color-neutral-white;

          @include mq($bp1200) {
            @include font-size(26, 40);
          }
        }
      }

      .f--link {
        @include margin(rem(50) null null);
        text-align: center;

        a {
          @include button;
        }
      }
    }
  }
}