.cc--full-width-media-cards {
  @include maxwidth(1440px);

  .c--full-width-media-cards {
    @include padding(rem(100) cols(1, 12) rem(50));

    @include mq($bp1024) {
      @include padding(rem(150) cols(6));
    }

    .f--section-title {
      @include margin(null null rem(45));
      text-align: center;

      @include mq($bp1024) {
        @include margin(null null rem(50));
      }

      h2 {
        @include font-regular;
        @include font-size(30, 40);
        color: $color-neutral-white;

        @include mq($bp1024) {
          @include font-size(60, 70);
        }
      }
    }

    .cards-container {
      @include mq($bp768) {
        display: flex;
      }

      .f--card {
        @include card-shadow;
        @include padding(null null rem(32));
        border: 1px solid $color-neutral-gray-27;
        border-radius: rem(16);

        @include mq($bp768) {
          flex: 0 0 cols(11, 36);
        }

        @include mq($bp1024) {
          @include padding(null null rem(52));
          border-radius: rem(24);
        }

        &:not(:last-child) {
          @include margin(null null rem(32));

          @include mq($bp768) {
            @include margin(null cols(1.5, 36) 0 null);
          }
        }

        .image-container {
          .f--image {
            @include margin(null null rem(20));

            @include mq($bp1024) {
              @include margin(null null rem(35));
            }

            img {
              border-radius: rem(16) rem(16) 0 0;

              @include mq($bp1024) {
                border-radius: rem(24) rem(24) 0 0;
              }
            }
          }
        }

        .logo-container {
          .f--image {
            @include margin(null null rem(20));

            @include mq($bp1024) {
              @include margin(null null rem(12));
            }
            
            img {
              @include margin(null auto);
              max-width: 100%;
              width: auto;
            }
          }
        }

        .f--description {
          @include margin(null null rem(20));
          @include padding(null rem(18) null);
          text-align: center;

          @include mq($bp1024) {
            @include padding(null rem(24) null);
          }

          p {
            @include font-regular;
            @include font-size(18, 24);
            @include margin(null null rem(20));
            color: $color-primary-light-1;

            @include mq($bp1024) {
              @include font-size(22, 28);
            }
          }
        }

        .byline {
          @include font-medium-italic;
          @include font-size(18, 20);
          color: $color-neutral-gray-72;
          display: block;
          text-align: center;
          text-decoration: none;

          @include mq($bp1024) {
            @include font-size(22, 24);
          }
        }
      }
    }
  }
}