.lc--two-column {
  .l--two-column {
    @include maxwidth;

    .l--two-column-inner {
      @include padding(rem(30) rem(20) null);

      @include mq($bp768) {
        @include padding(rem(40) cols(2) null);
        display: flex;
        flex-wrap: wrap;
      }

      .tc--program-listing & {
        @include padding(0 0 null);

        @include mq($bp768) {
          @include padding(null cols(2) null 0);
        }
      }

      .lr--sidebar {
        padding: 0;

        @include mq($bp768) {
          @include margin(null cols(2, 44) 0 null);
          flex-basis: cols(20, 44);

          .tc--program-listing & {
            flex-basis: cols(17);
          }
        }
      }

      .lr--main {
        @include mq($bp768) {
          flex-basis: cols(22, 44);

          .tc--program-listing & {
            flex-basis: cols(27);
          }
        }
      }
    }
  }

  .tc--event & {
    @include margin(rem(-35) null null);

    @include mq($bp768) {
      @include margin(rem(-30) null null);
    }

    @include mq($bp1024) {
      @include margin(rem(-54) null null);
    }

    .l--two-column {
      .l--two-column-inner {
        @include padding(0);

        .lr--sidebar {
          @include mq($bp768) {
            width: cols(13);
          }
        }

        .lr--main {
          @include padding(null cols(3));
          @include mq($bp768) {
            @include padding(null cols(2) null 0);
            width: cols(35);
          }

          @include mq($bp1024) {
            @include padding(null cols(3) null null);
          }
        }
      }
    }
  }
}
