.cc--full-width-sliders {
  @include padding(rem(100) cols(1, 12));
  overflow: hidden;
  position: relative;

  @include mq($bp1024) {
    @include padding(rem(150) cols(6));
  }

  .c--full-width-sliders {
    @include maxwidth(1440px);

    .cards-container {
      .swiper {
        overflow: visible;

        .swiper-slide {
          height: auto;
        }

        .swiper-pagination {
          bottom: rem(-40);

          .swiper-pagination-bullet {
            background: $color-neutral-gray-43;

            &.swiper-pagination-bullet-active {
              background: $color-primary-main;
            }
          }
        }
      }
    }
  }
}