.cc--logo-grid {
  position: relative;
  overflow: hidden;

  .c--logo-grid {
    @include padding(rem(100) cols(1, 12));

    @include mq($bp1024) {
      @include padding(rem(80) cols(10) rem(160));
    }
  }

  .grid-wrapper {
    @include maxwidth(1120px);
    column-gap: rem(35);
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    row-gap: rem(30);

    @include mq($bp768) {
      grid-template-columns: repeat(5, 1fr);
    }

    @include mq($bp1200) {
      column-gap: rem(60);
      row-gap: rem(24);
    }

    .f--image {
      &:last-child {
        display: none;

        @include mq($bp768) {
          display: block;
        }
      }
    }
  }
}