.cc--large-cta {
  display: none;

  @include mq($bp1200) {
    @include padding(null cols(6) rem(150));
    display: block;
    overflow: none;
    position: relative;
  }

  .c--large-cta {
    @include mq($bp1200) {
      @include maxwidth(1440px);
      @include padding(rem(132) null);
      background: $color-gradient-prime-darker;
      border-radius: rem(24);
    }

    .f--section-title {
      @include margin(null null rem(50));
      text-align: center;

      h2 {
        @include gradient-text($color-gradient-prime);
        @include font-semi-bold;
        @include font-size(80, 96);
        text-transform: uppercase;
      }
    }

    .f--link {
      text-align: center;

      a {
        @include button(true);
      }
    }
  }
}