.cc--news-cards {
  height: 100%;
  overflow: hidden;
  position: relative;

  .c--news-cards {
    @include card-shadow;
    @include padding(rem(50) cols(1, 10) rem(56));
    border: rem(1) solid $color-neutral-gray-27;
    border-radius: rem(16);
    height: 100%;
    position: relative;

    @include mq($bp1200) {
      @include padding(rem(100) rem(32) rem(50));
      border-radius: rem(24);
    }

    .f--cta-title {
      @include margin(null null rem(50));
      text-align: center;

      @include mq($bp1024) {
        @include margin(null null rem(32));
      }

      h3 {
        @include font-semi-bold;
        @include font-size(26, 34);
        color: $color-neutral-white;

        @include mq($bp1024) {
          @include font-size(40, 50);
        }
      }
    }

    .cards-container {
      .swiper {
        min-height: rem(225);
        overflow: hidden;

        .swiper-slide {
          height: auto;
        }

        .swiper-button-prev {
          background-image: url('data:image/svg+xml;base64,');
          background-position: center;
          background-repeat: no-repeat;
          left: 0;

          &::after {
            content: '';
          }
        }

        .swiper-button-next {
          background-image: url('data:image/svg+xml;base64,');
          background-position: center;
          background-repeat: no-repeat;
          right: 0;

          &::after {
            content: '';
          }
        }
      }

      .f--image {
        @include margin(null null rem(32));

        img {
          @include margin(null auto);
          max-width: cols(6, 10);
          width: auto;

          @include mq($bp1024) {
            width: rem(100);
          }
        }
      }

      .f--description {
        @include margin(null null rem(32));
        @include padding(null cols(1, 10));
        text-align: center;

        p {
          @include font-regular;
          @include font-size(18, 24);
          color: $color-primary-light-1;

          @include mq($bp1024) {
            @include font-size(26, 34);
          }
        }
      }

      .f--link {
        text-align: center;

        a {
          @include font-bold;
          @include font-size(22, 30);
          color: $color-primary-main;
          text-transform: uppercase;
          text-decoration: none;

          @include hover {
            text-decoration: underline;
          }
        }
      }
    }
  }
}