.lity {
  background: $color-neutral-black;

  .lity-wrap {
    @include padding(rem(75) null rem(75));
    overflow: auto;

    .lity-container {
      position: relative;
      width: 100%;
      max-width: $max-width;

      .lity-close {
        top: rem(25);
        right: rem(25);

        &:focus {
          outline: rem(1) solid $color-neutral-white;
        }
      }
    }
  }

  &.lity-youtube,
  &.lity-vimeo {
    .lity-content {
      @include margin(null auto);
      max-width: 80vw;
      max-height: 80vh;
    }
  }
}
