.cc--three-up-cards {
  overflow: hidden;
  position: relative;

  .c--three-up-cards {
    @include padding(rem(100) cols(1, 12));

    @include mq($bp768) {
      @include padding(null cols(6));
    }

    .inner-wrapper {
      @include maxwidth(1440px);
    }

    .f--section-title {
      @include margin(null null rem(32));
      text-align: center;

      @include mq($bp768) {
        @include margin(null null rem(70));
      }

      h2 {
        @include font-regular;
        @include font-size(30, 40);
        color: $color-neutral-white;

        @include mq($bp768) {
          @include font-size(60, 70);
        }
      }
    }

    .cards-container {
      .swiper {
        overflow: visible;

        .swiper-slide {
          height: auto;
        }

        .swiper-pagination {
          bottom: rem(-40);

          .swiper-pagination-bullet {
            background: $color-neutral-gray-43;

            &.swiper-pagination-bullet-active {
              background: $color-primary-main;
            }
          }
        }
      }

      .f--card {
        @include card-shadow;
        @include padding(rem(50) cols(1, 10));
        border: rem(1) solid $color-neutral-gray-27;
        border-radius: rem(16);
        height: 100%;
        position: relative;

        @include mq($bp1200) {
          @include padding(rem(100) rem(32) rem(50));
          border-radius: rem(24);
        }

        &:nth-child(2) {
          @include padding(null rem(32));
        }

        &:not(:last-child) {
          @include margin(null null rem(32));

          @include mq($bp1024) {
            @include margin(null cols(1.5, 36) 0 null);
          }
        }
      }
      
      .f--eyebrow {
        @include centered('x');
        top: rem(-20);

        span {
          @include font-semi-bold;
          @include padding(rem(6) rem(54));
          background-color: $color-neutral-gray-10;
          border: rem(1) solid $color-neutral-gray-27;
          border-radius: rem(20);
          color: $color-neutral-white;
          display: block;
          text-transform: uppercase;
          white-space: nowrap;

          @include mq($bp1024) {
            @include font-size(22, 28);
          }
        }
      }

      .f--image {
        @include margin(null null rem(32));

        img {
          @include margin(null auto);
          width: rem(70);

          @include mq($bp1024) {
            width: rem(100);
          }
        }
      }

      .f--cta-title {
        @include margin(null null rem(10));
        text-align: center;

        @include mq($bp1024) {
          @include margin(null null rem(32));
        }

        h3 {
          @include font-semi-bold;
          @include font-size(26, 34);
          color: $color-neutral-white;

          @include mq($bp1024) {
            @include font-size(40, 50);
          }
        }
      }

      .f--description {
        text-align: center;

        p {
          @include font-regular;
          @include font-size(18, 24);
          color: $color-primary-light-1;

          @include mq($bp1024) {
            @include font-size(26, 34);
          }
        }
      }
    }
  }
}